import React from "react";
import {sendMessageActionCreator} from "../../redux/dialogs-reducer";
import Dialogs from "./Dialogs";
import {connect} from "react-redux";
import {compose} from "redux";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {getDialogsSelector, getMessagesSelector, getNewMessageTextSelector} from "../../redux/dialogs-selectors";




let mapStateToProps = (state) => {
    return {
        messages:getMessagesSelector(state),
        dialogs:getDialogsSelector(state),
        newMessageText:getNewMessageTextSelector(state),
    }
}
let mapDispachToProps = (dispatch) => {
    return {
        sendMessage: (newMessageBody) => {
            dispatch(sendMessageActionCreator(newMessageBody))
        }
    }
}


export default compose(connect(mapStateToProps, mapDispachToProps), withAuthRedirect)(Dialogs)
